@import url('https://fonts.googleapis.com/css2?family=Mulish:wght@600&display=swap');
*{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    font-family: 'Mulish', sans-serif;
}

.navbar-brand{
    font-size: 1.9rem !important;
    color: #565387 !important;
}
.navbar a:hover{
    color: #3498db !important;
}
a.active_class{
    color: white;
    border-bottom: 1px solid hsl(0, 98%, 51%);
    
}
.menu_active{
    font-weight: bold;
    border-bottom: 1px solid #565387;
}
.menu_style{
    width: 100%;
    text-transform: bold;
    line-height: 60px;
    background-color: #20232a;
    display: flex;
    justify-content: space-around;
}
.searchbar{
    width: 50%;
    height: auto;
    margin: 0 auto;
    text-align: center;
}

.searchbar input{
    margin: 30px 0;
    padding: 5px 10px;
    border-radius: 5px;
    font-family: sans-serif;
}

#header{
    width: 100%;
    height: 80vh;
    margin-top: 70px;
}
#header h2{
    margin: 0 0 10px 0;
    font-size: 48px;
    font-weight: 700;
    color: black;
}
#header .brand-name{
    color: #3498db;
}

#header h5{
    color: hsl(256, 95%, 15%);
    font-size: 24px;
}
#header .btn-get-started{
    font-weight: 500;
    font-size: 16px;
    letter-spacing: 1px;
    display: inline-block;
    padding: 10px 30px;
    border-radius: 50px;
    transition: 0.5s;
    color: #3498db;
    border: 2px solid #3498db;
    text-decoration: none;
}
#header .btn-get-started:hover{
    background: #3498db;
    color: white;
}
#header .animated{
    animation: up-down 2s ease-in-out infinite alternate-reverse both;
}
#card{
    width: 400px;
    height: 400px;
}
@-webkit-keyframes up-down{
    0%{
        transform: translateY(10px);
    }
    100%{
        transform: translateY(-10px);
    }
}
@-webkit-keyframes up-down{
    0%{
        transform: translateY(10px);
    }
    100%{
        transform: translateY(-10px);
    }
}